.section.quote{
    display: block;
    .quote-text{
     font-size: 1.3rem;
     line-height: 1.3;
     color: var(--text-color);
      
     svg{
         color: var(--primary-color);
         font-size: 1.2rem;
         margin-right: .5rem;
     }
    }
    .quote-author{
     color: var(--text-light);
     text-align: right;
     margin-top: 1rem;
    }
 }