.section.improve-skill{
    .col.img{
       img{
        width: 100%;
       }
    }
    .col.typography{
        padding-left: 1rem;
    }
    .skill-item{
        border-left: .25rem solid var(--primary-color);
        margin-bottom: .5rem;
        padding: .1rem 0 .1rem .5rem;
        color: var(--text-light);
        font-weight: bold;
        transform: translateX(100%);
        opacity: 0;
        animation: getAnimationDuration(.3s) slideInRight forwards;
        @for $i from 1 to 8 {
            &:nth-child(#{$i}){
                animation-delay: #{$i * .1}s;
            }
        }
    }
    .btn{
        margin-top: 1rem;
    }
}